    /* overflow: scroll; */
}

.sidebar-bottom{
  position: absolute;
  height: 30vh;
  width: 350px;
  z-index: 999;
  right: 20px;
  top: 58vh;
  background-color: #fff;
  box-shadow: '0px 0px 15px rgba(0, 0, 0, 0.25)';
  border-radius: 3px;
  padding: 20px;
  overflow: scroll;
}


.tab {
    float: right;
    background-color: #ecf0f1;
    width: 15%;
    height: 95vh;

  }
  
  /* Style the buttons inside the tab */
  .tab button {
    display: block;
    background-color: inherit;
    color: black;
    padding: 22px 16px;
    width: 100%;
    border: none;
    outline: none;
    text-align: left;
    cursor: pointer;
    transition: 0.3s;
    font-size: 17px;
  }
  
  /* Change background color of buttons on hover */
  .tab button:hover {
    background-color: #ddd;
  }
  
  /* Create an active/current "tab button" class */
  .tab button.active {
    background-color: #ccc;
  }
  
  /* Style the tab content */
  .tabcontent {
    float: left;
    padding: 0px 12px;
    border: 1px solid #ccc;
    width: 70%;
    border-left: none;
    height: 300px;
  }

  .card{
    color: #fff;
    background-color: #fff;
    border: 2px solid #6c5ce7;
    /* box-shadow: rgb(0 0 0 / 25%) 0px 0px 15px; */
    margin-bottom: 1rem;
  }

  .card-body{
    padding: 10px;
    background-color: #6c5ce7;
  }

  .data-container {
    display: grid;
    grid-template-columns: auto auto auto;
    margin: 20px auto;
  }
  .data-item {
    background-color: rgba(255, 255, 255, 0.3);
    padding: 20px;
  }

  .btn-ext {
    font-family: 'Quicksand', sans-serif;
    width: 100%;
    border: 0px;
    padding: .8rem;
    color: rgb(255, 255, 255);
    font-weight: bold;
    font-size: 14px;
    cursor: pointer;
  }

  .customers {
    font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  
  .customers td, #customers th {
    border: 1px solid #ddd;
    padding: 8px;
  }
  
  .customers tr:nth-child(even){background-color: #f2f2f2;}
  
  .customers tr:hover {background-color: #ddd;}
  
  .customers th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: #04AA6D;
    color: white;
  }

  /* REACT SLICK */

  .cor-container{
    width: 100%;
    border-radius: 10px;
    margin-bottom: 5vh;
}

.cor-item{
    height: 200px;
    background-color: tomato;
    border-radius: 10px;
}

.toshow{
  display: flex;
  flex-direction: row;
  margin: 2vh 0 2vh 0;
}

.toshow div{
  width: 50%;
}

.changemapstyle{
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 5vw;
  aspect-ratio: 1;
  z-index: 999;
  left: 10px;
  bottom: 10px;
  background-color: #fff;
  box-shadow: '0px 0px 15px rgba(0, 0, 0, 0.25)';
  border-radius: 3px;
  padding: 2px;
  cursor: pointer;
  /* overflow: scroll; */
}

.changemapstyle img{
  width: 100%;
  border-radius: 3px;
}
